import {
  Component, OnInit, AfterViewInit, ViewEncapsulation, Output, EventEmitter, Input,
  OnChanges
} from '@angular/core';
declare var jQuery:any;
declare var moment: any;

@Component({
  selector: 'og-date-range-picker',
  templateUrl: './date_range_picker.component.html',
  encapsulation: ViewEncapsulation.None
})

export class DateRangePickerComponent implements  AfterViewInit, OnChanges {
  @Input() calc: any;
  @Output() date: EventEmitter <String> = new EventEmitter <String>();
  initialized: boolean = false;

  ngAfterViewInit() {
    var self:any = this;
    // TODO error in these two functions so commented it.
    //Data range Picker
    jQuery('.input-daterange-datepicker').daterangepicker({
      buttonClasses: ['btn', 'btn-sm'],
      applyClass: 'btn-danger',
      cancelClass: 'btn-inverse',
      startDate: this.calc?this.getUTCDate(this.calc.createdAt,'MM/DD/YYYY'):moment().subtract(10, 'days').calendar(),
      maxDate: this.getUTCDate(new Date(),'MM/DD/YYYY')
    });

    //On Date Apply
    jQuery('.input-daterange-datepicker').on('apply.daterangepicker', (ev:any, picker:any) => {
      self.date.emit({
        start_date: this.getUTCDate(new Date(picker.startDate.format('YYYY-MM-DD'))),
        end_date: this.getUTCDate(new Date(picker.endDate.format('YYYY-MM-DD')), 'YYYY-MM-DD', 1)
      });
    });

    this.initialized = true;
  }

  ngOnChanges(): void {
    if(this.initialized)
      jQuery('.input-daterange-datepicker').data('daterangepicker').setStartDate(
        this.calc?moment(this.calc.createdAt).format('MM/DD/YYYY'):moment().subtract(10, 'days').calendar()
      );
  }

  getUTCDate(date: any = new Date(), format: string = 'YYYY-MM-DD', addDays: number = 0) {
      return moment(date).utc().add(addDays,'days').format(format);
  }
}



